<template>
    <p-data-table
        :value="report"
        showGridlines
        class="p-datatable-sm p-mt-1 p-shadow-1"
        :loading="loading"
        :paginator="true"
        :rows="20"
        paginatorTemplate="CurrentPageReport FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
        currentPageReportTemplate="Showing {first} to {last} of {totalRecords} entries"
        :rowsPerPageOptions="[10, 20, 50]"
        responsiveLayout="scroll"
    >
        <p-column header="Patient Name" sortable>
            <template #body="{data}">
                {{ data.first_name }} {{ data.last_name }}
            </template>
        </p-column>
        <p-column header="Chart #" field="chartid" sortable />
        <p-column header="DOB" field="dob" />
        <p-column header="Semen #" field="semen_id" />
        <p-column header="Semen Date" field="semen_date" sortable />
        <p-column header="Appt Date" field="appointment_date" />
        <p-column header="OS Status" field="osStatus" />
        <p-column header="OS Type" field="osType" />
        <p-column header="Location" field="location" />
        <p-column header="Cycle Doctor" field="cycleDoctor" sortable />
        <p-column
            header="Demographic Doctor"
            field="demographicDoctor"
            sortable
        />
    </p-data-table>
</template>

<script>
import { useCycleReportTable } from "@/composable/cycle-report";
import { computed, onMounted, onUnmounted, ref } from "vue";
import { emitter } from "@/event-bus";
export default {
    setup() {
        const { store, getCycleReport, loading } = useCycleReportTable();

        const cycleDoctors = ref([]);
        const demographicDoctors = ref([]);

        const report = computed(() => store.state.cycleReport.report);

        const cycleDoctorChangeListener = (doctors) => {
            cycleDoctors.value = doctors;
        };

        const demographicDoctorChangeListener = (doctors) => {
            demographicDoctors.value = doctors;
        };

        onMounted(() => {
            emitter.on("cycle-report-table:get-cycle-report", getCycleReport);
            emitter.on(
                "cycle-report-form:cyecle-doctor-change",
                cycleDoctorChangeListener
            );
            emitter.on(
                "cycle-report-form:demographic-doctor-change",
                demographicDoctorChangeListener
            );
        });

        onUnmounted(() => {
            emitter.off("cycle-report-table:get-cycle-report", getCycleReport);
            emitter.off(
                "cycle-report-form:cyecle-doctor-change",
                cycleDoctorChangeListener
            );
            emitter.off(
                "cycle-report-form:demographic-doctor-change",
                demographicDoctorChangeListener
            );
        });

        return {
            report,
            loading
        };
    }
};
</script>
