<template>
    <main id="cycle-report" class="p-inputtext-sm">
        <div class="container">
            <div class="left-pane" v-if="showLeftPane">
                <cycle-report-form />
            </div>
            <div class="right-pane">
                <p-button @click="toggleLeftPane" icon="pi pi-bars" class="custom-button"></p-button>
                <semen-report-table v-if="reportType == 'semen'" />
                <patient-visit-table v-else-if="reportType == 'patientVisit'" />
                <cycle-report-table v-else />
            </div>
        </div>
    </main>
</template>

<script>
import { onMounted, ref } from "vue";

import CycleReportForm from "@/components/cycle-report/form.vue";
import CycleReportTable from "@/components/cycle-report/table.vue";
import SemenReportTable from "@/components/cycle-report/table-semen.vue";
import PatientVisitTable from "@/components/cycle-report/table-patient-visit.vue";
import { useStore } from "vuex";
import { computed } from "vue-demi";

export default {
    components: {
        CycleReportForm,
        CycleReportTable,
        SemenReportTable,
        PatientVisitTable
    },
    setup() {
        const store = useStore();
        onMounted(() => {
            store.dispatch("provider/getProviders");
        });

        const reportType = computed(() => store.state.cycleReport.reportType);
        const showLeftPane = ref(true);

        const toggleLeftPane = () => {
            showLeftPane.value = !showLeftPane.value;
        };

        return {
            reportType,
            showLeftPane,
            toggleLeftPane
        };
    }
};
</script>

<style>
html, body, #app, #cycle-report, .container {
    height: 100%;
    margin: 0;
    padding: 0;
}

#cycle-report .p-card .p-card-content {
    padding: 0 0;
}

#cycle-report .p-field {
    margin-bottom: 0.5rem;
}

#cycle-report .p-card {
    height: 100%;
}

.p-calendar,
.p-multiselect {
    height: 24.5px;
}

.container {
    display: flex;
    width: 100%;
    height: 100vh;
}

.left-pane {
    width: 300px;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    height: 100%;
}

.left-pane > * {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.right-pane {
    flex-grow: 1;
    padding-left: 5px; 
}

.custom-button {
    border: none; /* Optional: to remove the border */
    width: 20px; /* Set your desired width */
    height: 20px; /* Set your desired height */
    display: flex; /* To center icon */
    justify-content: center; /* To center icon */
    align-items: center; /* To center icon */
}

</style>
