import { DATE_FORMAT_STRING } from "@/config";
import { emitter } from "@/event-bus";
import { format } from "date-fns";
import { computed, reactive, ref } from "vue";
import { useStore } from "vuex";
import useVuelidate from "@vuelidate/core";
import { required } from "@vuelidate/validators";
import { useToast } from "primevue/usetoast";

export function useCycleReportForm() {
    const store = useStore();
    const form = reactive({
        reportType: store.state.cycleReport.reportType,
        cycleTypes: [],
        funded: null,
        from: null,
        to: null,
        cancelled: null,
        spermDonor: null,
        siteId: null,
        cycleDoctor: [],
        demographicDoctor: [],
        comments: null
    });

    // const form = computed(() => {
    //     return store.getters["dailyReport/getForm"];
    // });

    const form2 = reactive({
        cycleTypes: [],
        date: null,
        siteId: null,
    });

    // const form2 = computed(() => {
    //     return store.getters["dailyReport/getForm2"];
    // });

    const cycleTypesOpntions = computed(
        () => store.state.cycleReport.cycleTypes
    );

    const updateReportType = (rtype) => {
        store.dispatch("cycleReport/updateReportType", rtype.value);
        store.dispatch("cycleReport/emptyTheReport", []);
    };
    const fundedOptions = ref([
        { label: "All", value: -1 },
        { label: "Yes", value: 1 },
        { label: "No", value: 0 }
    ]);
    const spermDonorOptions = ref([
        { label: "All", value: "" },
        { label: "Yes", value: "Donor" },
        { label: "No", value: "Non-Donor" }
    ]);
    const reportTypes = ref([
        { label: "Cycle", value: "cycle" },
        { label: "Semen", value: "semen" },
        { label: "Patient Visit", value: "patientVisit" }
    ]);
    const cancelOptions = ref([
        { label: "All", value: "all" },
        { label: "Yes", value: "true" },
        { label: "No", value: "false" }
    ]);
    const locationOptions = computed(() => {
        return [
            { id: -1, name: "All" },
            { id: 0, name: "None" },
            ...store.state.provider.locations
        ];
    });
    const cycleDcotorOptions = computed(() => store.state.doctor.doctors);
    const demographicDcotorOptions = computed(() => store.state.doctor.doctors);

    const rules = {
        //cycleTypes: { required },
        //funded: { required },
        from: { required },
        to: { required },
        //cancelled: { required },
        //siteId: { required }
    };

    const rules2 = {
        date: { required },
        siteId: { required }
    };

    const v$ = useVuelidate(rules, form);
    const v2$ = useVuelidate(rules2, form2);

    const submitted = ref(false);
    const submitted2 = ref(false);


    const getCycleReport = () => {
        submitted2.value = false;
        form2.cycleTypes = [];
        form2.date = null;
        form2.siteId = null;

        submitted.value = true;
        if (v$.value.$invalid) {
            return;
        }

        emitter.emit("cycle-report-table:get-cycle-report", {
            ...form,
            searchComments: form.comments,
            cycleTypes: Object.values(form.cycleTypes),
            to: format(form.to, DATE_FORMAT_STRING),
            from: format(form.from, DATE_FORMAT_STRING)
        });
    };

    const getCycleDailyReport = () => {
        submitted.value = false;
        form.cycleTypes = [];
        form.funded = null;
        form.from = null;
        form.to = null;
        form.cancelled = null;
        form.spermDonor = null;
        form.siteId = null;
        form.cycleDoctor = [];
        form.demographicDoctor = [];

        submitted2.value = true;
        if (v2$.value.$invalid) {
            return;
        }

        emitter.emit("cycle-report-table:get-daily-cycle-report", {
            ...form2,
            searchComments: form.comments,
            cycleTypes: Object.values(form2.cycleTypes),
            date: format(form2.date, DATE_FORMAT_STRING)
        });
    };


    const loading = ref(false);
    const loading2 = ref(false);
    const getReportExcel = () => {

        submitted2.value = false;
        form2.cycleTypes = [];
        form2.date = null;
        form2.siteId = null;

        submitted.value = true;
        if (v$.value.$invalid) {
            return;
        }
        loading.value = true;
        store
            .dispatch("cycleReport/getCycleReportExcel", {
                ...form,
                searchComments: form.comments,
                cycleTypes: Object.values(form.cycleTypes),
                to: format(form.to, DATE_FORMAT_STRING),
                from: format(form.from, DATE_FORMAT_STRING)
            })
            .then((data) => {
                window.open(data.link, "_blank");
            })
            .finally(() => (loading.value = false));
    };

    const getDailyReportExcel = () => {

        submitted.value = false;
        form.cycleTypes = [];
        form.funded = null;
        form.from = null;
        form.to = null;
        form.cancelled = null;
        form.spermDonor = null;
        form.siteId = null;
        form.cycleDoctor = [];
        form.demographicDoctor = [];

        submitted2.value = true;
        if (v2$.value.$invalid) {
            return;
        }
        loading2.value = true;
        store
            .dispatch("cycleReport/getDailyCycleReportExcel", {
                ...form2,
                searchComments: form.comments,
                cycleTypes: Object.values(form2.cycleTypes),
                date: format(form2.date, DATE_FORMAT_STRING)
            })
            .then((data) => {
                window.open(data.link, "_blank");
            })
            .finally(() => (loading2.value = false));
    };

    const getLocationOption = () => {
        store.dispatch("cycleReport/getLocationOption");
    };

    const getAllCycleTypes = () => {
        store.dispatch("cycleReport/getAllCycleTypes");
    };

    return {
        form,
        form2,
        cycleTypesOpntions,
        fundedOptions,
        spermDonorOptions,
        reportTypes,
        cancelOptions,
        locationOptions,
        cycleDcotorOptions,
        demographicDcotorOptions,
        getCycleReport,
        getCycleDailyReport,
        getAllCycleTypes,
        getLocationOption,
        submitted, submitted2,
        v$, v2$,
        getReportExcel,
        getDailyReportExcel,
        updateReportType,
        loading, loading2
    };
}

export function useCycleReportTable() {
    const store = useStore();
    const toast = useToast();
    const loading = ref(false);
    const getCycleReport = (reqPayload) => {
        loading.value = true;
        store
            .dispatch("cycleReport/getCycleReport", reqPayload)
            .then(() => { })
            .catch(() => {
                toast.add({
                    severity: "warn",
                    summary: "",
                    detail: "No Data Found",
                    life: 3000
                });
            })
            .finally(() => {
                loading.value = false;
            });
    };

    const getCycleDailyReport = (reqPayload) => {
        loading.value = true;
        store
            .dispatch("cycleReport/getDailyCycleReport", reqPayload)
            .then(() => { })
            .catch(() => {
                toast.add({
                    severity: "warn",
                    summary: "",
                    detail: "No Data Found",
                    life: 3000
                });
            })
            .finally(() => {
                loading.value = false;
            });
    };

    return {
        store,
        loading,
        getCycleReport,
        getCycleDailyReport
    };
}
