<template>
    <p-data-table
        :value="report"
        showGridlines
        class="p-datatable-sm p-mt-1 p-shadow-1"
        :loading="loading"
        :paginator="true"
        :rows="20"
        paginatorTemplate="CurrentPageReport FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
        currentPageReportTemplate="Showing {first} to {last} of {totalRecords} entries"
        :rowsPerPageOptions="[10, 20, 50]"
        responsiveLayout="scroll"
        selectionMode="single"
        dataKey="chartid"
        v-model:selection="selectedReport"
        @selection-change="handleSelectionChange"
        @row-dblclick="viewCycleMonitoring"
    >
        <p-column header="Patient Name" sortable>
            <template #body="{data}">
                {{ data.first_name }} {{ data.last_name }}
            </template>
        </p-column>
        <p-column header="Chart #" field="chartid" sortable />
        <p-column header="DOB" field="dob" />
        <p-column header="Cycle #" field="cycle_id" />
        <p-column header="Cycle Type" field="cycle_type" />
        <p-column header="Funded" field="funded" />
        <p-column header="Cancelled" field="cycle_canceled" />
        <p-column header="Cycle Date" field="cycle_date" sortable />
        <p-column header="Cycle Comp Date" field="cycle_comp_date" />
        <p-column header="Appt Date" field="appointment_date" />
        <p-column header="OS Status" field="osStatus" />
        <p-column header="OS Type" field="osType" />
        <p-column header="Location" field="location" />
        <p-column header="Cycle Doctor" field="cycleDoctor" sortable />
        <p-column
            header="Demographic Doctor"
            field="demographicDoctor"
            sortable
        />
    </p-data-table>
</template>

<script>
import { useCycleReportTable } from "@/composable/cycle-report";
import { useDailyReportReportsInit } from "@/composable/daily-report";
import { computed, onMounted, onUnmounted, ref } from "vue";
import { emitter } from "@/event-bus";
import { useRouter } from "vue-router";
import { useToast } from "primevue/usetoast";

export default {
    setup() {
        const {
            store,
            getCycleReport,
            getCycleDailyReport,
            loading
        } = useCycleReportTable();

        const toast = useToast();

        const selectedReport = ref(null);

        const router = useRouter();

        const cycleDoctors = ref([]);
        const demographicDoctors = ref([]);
        const selectedRow = ref(null);

        const report = computed(() => store.state.cycleReport.report);

        const cycleDoctorChangeListener = (doctors) => {
            cycleDoctors.value = doctors;
        };

        const demographicDoctorChangeListener = (doctors) => {
            demographicDoctors.value = doctors;
        };

        const viewCycleMonitoring = () => {
            emitter.emit("daily-report-report:view-cycle-monitoring");
        };

        const handleSelectionChange = (selectedItems) => {
            if (selectedItems && selectedItems.length > 0) {
                // Assuming you want to select the first item when multiple items are selected
                // You might want to handle this differently based on your requirements
                selectedReport.value = selectedItems[0];
            } else {
                selectedReport.value = null; // Clear selected report if none is selected
            }
        };

        const showSelectRowToast = () => {
            toast.add({
                severity: "warn",
                summary: "Warning",
                detail: "Please Select a row first",
                life: 3000
            });
        };

        const showCycleMonitoring = () => {
            console.log(selectedReport);

            // if (!store.getters["dailyReport/isAnyReportSelected"]) {
            //     showSelectRowToast();
            //     return;
            // }
            // cycleMonitoringBtnLoading.value = true;

            let queryParam = {
                patientId: selectedReport.value.chartid,
                cycleId: selectedReport.value.cycle_id,
                to: "cycle-monitoring"
            };

            router.push({
                name: "redirect",
                query: queryParam
            }); // zubaer added this
        };

        onMounted(() => {
            emitter.on("cycle-report-table:get-cycle-report", getCycleReport);
            emitter.on(
                "cycle-report-table:get-daily-cycle-report",
                getCycleDailyReport
            );
            emitter.on(
                "cycle-report-form:cyecle-doctor-change",
                cycleDoctorChangeListener
            );
            emitter.on(
                "cycle-report-form:demographic-doctor-change",
                demographicDoctorChangeListener
            );
            emitter.on(
                "daily-report-report:view-cycle-monitoring",
                showCycleMonitoring
            );
        });

        onUnmounted(() => {
            emitter.off("cycle-report-table:get-cycle-report", getCycleReport);
            emitter.off(
                "cycle-report-table:get-daily-cycle-report",
                getCycleDailyReport
            );
            emitter.off(
                "cycle-report-form:cyecle-doctor-change",
                cycleDoctorChangeListener
            );
            emitter.off(
                "cycle-report-form:demographic-doctor-change",
                demographicDoctorChangeListener
            );
            emitter.off(
                "daily-report-report:view-cycle-monitoring",
                showCycleMonitoring
            );

            document.removeEventListener("dblclick", viewCycleMonitoring);
        });

        return {
            report,
            loading,
            selectedReport,
            viewCycleMonitoring,
            showCycleMonitoring,
            handleSelectionChange,
            showSelectRowToast
        };
    }
};
</script>
