<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-formgrid p-grid">
                <div class="p-field p-col-12 p-md-12">
                    <label for="cycle-type" class="p-mb-1">
                        Report Type
                    </label>
                    <p-dropdown
                        v-model="form.reportType"
                        :options="reportTypes"
                        optionLabel="label"
                        optionValue="value"
                        @change="updateReportType"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="form.reportType !== 'semen'"
                >
                    <label for="cycle-type" class="p-mb-1">
                        Cycle Type
                    </label>
                    <p-multi-select
                        v-model="form.cycleTypes"
                        :options="cycleTypesOpntions"
                        optionLabel="cycleType"
                        optionValue="cycleType"
                    />
                </div>

                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="form.reportType == 'cycle'"
                >
                    <label class="p-mb-1"> Funded </label>
                    <p-dropdown
                        v-model="form.funded"
                        :options="fundedOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="form.reportType == 'cycle'"
                >
                    <label class="p-mb-1">
                        Cancel
                    </label>
                    <p-dropdown
                        v-model="form.cancelled"
                        :options="cancelOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>

                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="form.reportType == 'semen'"
                >
                    <label for="cycle-canceled" class="p-mb-1">
                        Sperm Donor
                    </label>
                    <p-dropdown
                        v-model="form.spermDonor"
                        :options="spermDonorOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>

                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="form.reportType !== 'patientVisit'"
                >
                    <label for="cycle-canceled" class="p-mb-1">
                        Location
                    </label>
                    <p-dropdown
                        v-model="form.siteId"
                        :options="locationOptions"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div>

                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="form.reportType == 'patientVisit'"
                >
                    <label class="p-mb-1"> Funded </label>
                    <p-dropdown
                        v-model="form.funded"
                        :options="fundedOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>

                <div class="p-field p-col-12 p-md-6">
                    <label class="p-mb-1">From Date</label>
                    <p-calendar
                        :showIcon="true"
                        v-model="form.from"
                        dateFormat="yy-mm-dd"
                        :class="{
                            'p-invalid': submitted && v$.from.$invalid
                        }"
                    />
                </div>
                <div class="p-field p-col-12 p-md-6">
                    <label class="p-mb-1">To Date</label>
                    <p-calendar
                        :showIcon="true"
                        v-model="form.to"
                        dateFormat="yy-mm-dd"
                        :class="{
                            'p-invalid': submitted && v$.to.$invalid
                        }"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="form.reportType !== 'patientVisit'"
                >
                    <label for="cycle-type" class="p-mb-1">
                        Cycle Doctor
                    </label>
                    <p-multi-select
                        v-model="form.cycleDoctor"
                        :options="cycleDcotorOptions"
                        :filter="true"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div>
                <div class="p-field p-col-12 p-md-6">
                    <label for="cycle-type" class="p-mb-1">
                        Demographic Doctor
                    </label>
                    <p-multi-select
                        v-model="form.demographicDoctor"
                        :options="demographicDcotorOptions"
                        :filter="true"
                        optionLabel="name"
                        optionValue="id"
                    />
                </div>
                <div class="p-field p-col-12 p-md-12 button-container">                      
                    <p-button label="Get Report" @click="getCycleReport" class="custom-button"/>
                    <p-button
                        label="Export XLS"
                        @click="getReportExcel"
                        :loading="loading"
                        class="custom-button"
                    />
                </div>
            </div>
            <hr class="p-mt-4 p-mb-4">
            <div
                class="p-fluid p-formgrid p-grid"
                v-if="form.reportType == 'cycle'"
            >
                <div class="p-field p-col-12 p-md-6">
                    <label for="cycle-type" class="p-mb-1">
                        Cycle Type
                    </label>
                    <p-multi-select
                        v-model="form2.cycleTypes"
                        :options="cycleTypesOpntions"
                        optionLabel="cycleType"
                        optionValue="cycleType"
                    />
                </div>

                <div class="p-field p-col-12 p-md-6">
                    <label for="cycle-canceled" class="p-mb-1">
                        Location
                    </label>
                    <p-dropdown
                        v-model="form2.siteId"
                        :options="locationOptions"
                        optionLabel="name"
                        optionValue="id"
                        :class="{
                            'p-invalid': submitted2 && v2$.siteId.$invalid
                        }"
                    />
                </div>

                <div class="p-field p-col-12 p-md-6">
                    <label class="p-mb-1">Date</label>
                    <p-calendar
                        :showIcon="true"
                        v-model="form2.date"
                        dateFormat="yy-mm-dd"
                        :class="{
                            'p-invalid': submitted2 && v2$.date.$invalid
                        }"
                    />
                </div>
                
                <div class="p-field p-col-12 p-md-12 button-container">
                    <p-button label="Get Report" @click="getCycleDailyReport" class="custom-button" />
                    <p-button label="Export XLS" @click="getDailyReportExcel" :loading="loading2" class="custom-button" />
                </div>                         
            </div>
            <hr class="p-mt-4 p-mb-4">
            <div
                class="p-fluid p-formgrid p-grid"
                v-if="form.reportType == 'cycle'"
            >
                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="form.reportType == 'cycle'"
                >
                <label for="cycle-type" class="p-mb-1">
                    Search In Comments
                </label>
                <p-input-text v-model="form.comments" />
                </div>

                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="form.reportType == 'cycle'"
                >
                    <label class="p-mb-1"> Pregnancy </label>
                    <p-dropdown
                        v-model="form.pregnancy"
                        :showClear="true"
                        :options="pregnancyOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
                <div
                    class="p-field p-col-12 p-md-6"
                    v-if="form.reportType == 'cycle'"
                >
                    <label class="p-mb-1"> Protocol </label>
                    <p-multi-select
                        v-model="form.protocol"
                        :showClear="true"
                        :options="protocolOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { useCycleReportForm } from "@/composable/cycle-report";
import { emitter } from "@/event-bus";
import { onMounted, watch } from "vue";
import { inject, ref } from "vue";

export default {
    setup() {
        const {
            form,
            form2,
            cycleTypesOpntions,
            fundedOptions,
            reportTypes,
            spermDonorOptions,
            cancelOptions,
            locationOptions,
            cycleDcotorOptions,
            demographicDcotorOptions,
            getCycleReport,
            getCycleDailyReport,
            getAllCycleTypes,
            getLocationOption,
            submitted,
            submitted2,
            v$,
            v2$,
            getReportExcel,
            getDailyReportExcel,
            updateReportType,
            loading,
            loading2
        } = useCycleReportForm();

        const pregnancyOptions = ref([
            { label: "Positive", value: 1 },
            { label: "Negative", value: 0 }
        ]);

        const protocolOptions = ref([
            { label: "Natural cycle", value: "Natural Cycle" },
            {
                label: "Modified natural cycle",
                value: "Modified natural cycle"
            },
            { label: "Mild Stimulation", value: "Mild Stimulation" },
            { label: "Antagonist", value: "Antagonist" },
            { label: "Long agonist", value: "Long agonist" },
            { label: "Flare agonist", value: "Flare agonist" },
            {
                label: "Microdose flare agonist",
                value: "Microdose flare agonist"
            },
            { label: "IVM protocol", value: "IVM protocol" },
            { label: "High Estrace", value: "High Estrace" },
            { label: "Regular Estrace", value: "Regular Estrace" },
            { label: "Other", value: "Other" }
        ]);

        const cycleDoctorChangeListener = ({ value }) => {
            emitter.emit("cycle-report-form:cyecle-doctor-change", value);
        };

        const demograhicDoctorChangeListener = ({ value }) => {
            emitter.emit("cycle-report-form:demographic-doctor-change", value);
        };

        onMounted(() => {
            getAllCycleTypes();
            getLocationOption();
        });

        return {
            pregnancyOptions,
            protocolOptions,
            loading2,
            form,
            form2,
            cycleTypesOpntions,
            fundedOptions,
            reportTypes,
            spermDonorOptions,
            cancelOptions,
            locationOptions,
            cycleDcotorOptions,
            demographicDcotorOptions,
            getCycleReport,
            getCycleDailyReport,
            submitted,
            submitted2,
            v$,
            v2$,
            getReportExcel,
            getDailyReportExcel,
            updateReportType,
            loading,
            cycleDoctorChangeListener,
            demograhicDoctorChangeListener
        };
    }
};
</script>

<style scoped>
.button-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.custom-button {
    flex: 1;
    margin-right: 1rem;
    height: 34px;
    line-height: 40px;
    padding: 0;
}

.custom-button:last-child {
    margin-right: 0;
}
</style>
